import { Typography } from '@mui/material';
import React from 'react'
import coder from "../images/coder.svg";
import res from "../images/Resume.pdf";


function Compo4() {
  return (
    <div className='sec'>
      <Typography 
      variant="h4" 
      component="h2" 
      fontFamily={'Handlee','cursive'}
      fontSize='40px'
      display='flex'
      justifyContent='center'
      color= 'orchid'
      mb={2}
  >
        OK here's my Resume
      </Typography>
      <div className='res'>
        <iframe src={res} width="450px" height="620px" >
        </iframe>
      </div>
      <div className='coderimg'>
        <img src={coder} width="345px"/>
      </div>
      <hr></hr>

    </div>
    
  )
}

export default Compo4
